label{
  font-weight: bold;
  font-family:Tahoma;
}
.buttons{
  display: inline-flex;
}
.close{
  background-color: grey;
  width:100px;
}
.bill{
  width:100px ;
}
h2{
  color: red;
  margin-left: 37%;
  margin-bottom: 0px;
  font-weight:17px;
  font-family:'Lucida Sans';
}

.pow{
  width: 400px;
  margin-top: 0px;
  height: 200px;
}

.functionalities{
 display:block;
}


@media only screen and (max-width: 600px) {
  button {
    width:95%;
   
  }
  
  .functionalities{
    margin:0 auto;
    text-align: center;
    padding: 0px;
  }
  table{
    width: 100%;
    padding:10px
  }
  .chart{
    display: block;
    margin-right: auto;
    margin-left: auto;
  }
  .logout,.btn-reset{
    display: none;
  }
 
}
